import { useRef } from "react";
import React, { Container } from "@chakra-ui/react";
import { AddIcon } from "@chakra-ui/icons";
import { useTodo } from "../hooks/useTodo";
import { TodoTitle } from "./TodoTitle";
import { TodoAdd } from "./TodoAdd";
import { TodoList } from "./TodoList";

function App() {
  const {
    todoList,
    AddtTodoListItem,
    toggleTodoListItemStatus,
    deleteTodoListItem,
  } = useTodo();

  const inputEl = useRef<HTMLTextAreaElement>(null);

  const handAddTodoListItem = () => {
    if (inputEl.current?.value === "") return;
    if (inputEl.current?.value) {
      AddtTodoListItem(inputEl.current?.value);
      inputEl.current.value = "";
    }
  };

  console.log("TODO:", todoList);
  const inCompleterdList = todoList.filter((todo) => {
    return !todo.done;
  });
  console.log(":", inCompleterdList);

  const CompleterdList = todoList.filter((todo) => {
    return todo.done;
  });
  console.log(":", CompleterdList);
  return (
    <>
      <Container centerContent p={{ base: "4", md: "6" }} maxWidth="3xl">
        <TodoTitle
          title="Todo"
          as="h1"
          fontsize={{ base: "2xl", md: "3xl" }}
        />
        <TodoAdd
          inputEl={inputEl}
          handleAddTodoListItem={handAddTodoListItem}
          placeholder="ADD TODO"
          buttonText="TODO"
          leftIcon={<AddIcon />}
        />
        <TodoList
          todoList={inCompleterdList}
          toggleTodoListItemStatus={toggleTodoListItemStatus}
          deleteTodoListItem={deleteTodoListItem}
          title=""
          as="h2"
          fontsize={{ base: "xl", md: "2xl" }}
        />
        <TodoList
          todoList={CompleterdList}
          toggleTodoListItemStatus={toggleTodoListItemStatus}
          deleteTodoListItem={deleteTodoListItem}
          title=""
          as="h2"
          fontsize={{ base: "xl", md: "2xl" }}
        />
      </Container>
    </>
  );
}

export default App;
